/**
 * define data model properties
 */
export default () => ({
  type: 'object',
  properties: {
    name: {
      type: 'string',
      title: '',
    },
    dataType: {
      type: 'string',
      title: '',
      enum: [
        '', 'string', 'number', 'boolean', 'regexp', 'array',
        'date', 'url', 'email',
      ],
      'ui:hidden': false
    },
    rules: {
      type: 'array',
      title: '',
      description: '',
      items: {
        type: 'object',
        properties: {
          name: {
            title: '',
            type: 'string',
            enum: ['required', 'max', 'min', 'pattern'],
            enumNames: ['', '', '', '']
          },
          required: {
            title: '',
            type: 'boolean',
            'ui:widget': 'switch',
            'ui:hidden': (value: boolean, rootValue: any, formData: object) => rootValue.name !== 'required'
          },
          max: {
            title: '',
            type: 'string',
            'ui:hidden': (value: boolean, rootValue: any, formData: object) => rootValue.name !== 'max'
          },
          min: {
            title: '',
            type: 'string',
            'ui:hidden': (value: boolean, rootValue: any, formData: object) => rootValue.name !== 'min'
          },
          pattern: {
            title: '',
            type: 'string',
            'ui:hidden': (value: boolean, rootValue: any, formData: object) => rootValue.name !== 'pattern'
          },
          message: {
            title: '',
            type: 'string'
          }
        }
      },
      "ui:options": {
        "foldable": true
      },
    }
  }
})
